.button {
  padding: 15px 35px;
  margin: 10px;
  background-color: white;
  color: black;

  &:hover {
    background-color: aquamarine;
  }
}

.form {
  &_section {
    display: flex;
    flex-direction: column;
    gap: 20px;

    &__question {
      display: flex;
      flex-direction: row;
      justify-content: flex-start;
      align-items: center;
      gap: 20px;

      @media (max-width: 1000px) {
        flex-direction: column;
        align-items: flex-start;
        justify-content: center;
      }

      &__answers {
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        align-items: center;
        gap: 20px;

        @media (max-width: 390px) {
          flex-direction: column;
          align-items: flex-start;
          justify-content: center;
          gap: 10px;
        }
      }

      &__wrong {
        background-color: rgb(245, 187, 187);
        border-radius: 30px 0 0 30px;
      }

      &__right {
        background-color: rgb(195, 241, 195);
        border-radius: 30px 0 0 30px;
      }
    }
  }
}

.image-option {
  height: 100px;
  width: 100px;
  object-fit: cover;

  @media (max-width: 500px) {
    height: 50px;
    width: 50px;
  }
}

.timer-container {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 300px;
  margin-bottom: 20px;
}

.timer-box {
  background-color: #f5f5f5;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.timer-box h1 {
  font-size: 36px;
  margin: 0;
}

.timer-box p {
  font-size: 14px;
  margin: 0;
  color: #888;
}
.colon {
  font-size: 2rem;
  margin: 0 5px;
}

.button-container button {
  font-size: 16px;
  margin: 0 10px;
  padding: 10px 20px;
  cursor: pointer;
  border: none;
  border-radius: 5px;
  background-color: #4caf50;
  color: white;
  transition: background-color 0.3s;
}

.button-container button:hover {
  background-color: #45a049;
}

