.nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.menu {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  gap: 30px;

  &--opened {
    height: 100vh;
    scroll-behavior: none;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
  }

  &__container {
    width: 100%;
    display: flex;
    padding: 10px;
    justify-content: flex-start;
    align-items: center;

    @media (min-width: 1001px) {
      padding: 30px;
    }
  }

  &__list {
    display: flex;
    gap: 20px;
    @media (max-width: 1000px) {
      display: none;
    }
  }

  &__icon {
    @media (min-width: 1001px) {
      display: none;
    }
  }

  &__mobile {
    display: none;
    &--opened {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
  }
}

.logo__image {
  height: 30px;
}

.menu__image {
  height: 30px;
  box-sizing: border-box;

  &--cross {
    padding: 2px;
  }
}
